import * as THREE from 'three';
import React, { useState, useEffect } from 'react';
import fetchData from './fetchData';
import { Canvas } from '@react-three/fiber';
import { Stats, OrbitControls } from '@react-three/drei';
import CircleGroup from './CircleGroup';

const App = () => {
  const [groupedData, setGroupedData] = useState({});

  const sphereGeometry = new THREE.SphereGeometry(0.075, 8);

  useEffect(() => {
    const getData = async () => {
      const fetchedData = await fetchData();
      // Group data by clusters
      const grouped = fetchedData.reduce((acc, item) => {
        const cluster = item.clusters;
        if (!acc[cluster]) {
          acc[cluster] = [];
        }
        acc[cluster].push(item);
        return acc;
      }, {});
      setGroupedData(grouped);
    };

    getData();
  }, []);
  console.log(groupedData);

  // Use `data` for your components, e.g., pass to CircleGroup as needed

  return (
    <div style={{ height: "100vh" }}>
      <Stats />
      <Canvas style={{ background: 'black' }}>
        <ambientLight intensity={5} />
        {/* <pointLight position={[10, 10, 10]} /> */}
        {/* Render each group as a CircleGroup */}
        {Object.keys(groupedData).map((cluster, index) => {
          // const positions = groupedData[cluster].map(item => [item.X_umap0_norm, item.X_umap1_norm, 0]);
          const positions = groupedData[cluster].map(item => [item.global_sphere0_norm * 20, item.global_sphere1_norm * 20, item.global_sphere2_norm * 20])
          // const positions = groupedData[cluster].map(item => [item.X_umap0_norm, item.X_umap1_norm, item.global_sphere2_norm]);
          const color = groupedData[cluster][0].clusters_colors; // Assuming the same color for all items in the cluster
          return <CircleGroup key={index} positions={positions} color={color} geometry={sphereGeometry} />;
        })}
        <OrbitControls 
          enableZoom={true} 
          enablePan={true} 
          enableRotate={true} 
          panSpeed={1} 
          rotateSpeed={1} 
          zoomSpeed={1}
          mouseButtons={{
            LEFT: THREE.MOUSE.ROTATE,
            MIDDLE: THREE.MOUSE.DOLLY,
            RIGHT: THREE.MOUSE.PAN
          }} 
        />
      </Canvas>
    </div>
  );
};

export default App;
