// src/CircleMesh.js
import React from 'react';

function CircleMesh({ material, geometry, position, color, scale, onPointerOver, onPointerOut }) {
  return (
    <mesh
      position={position}
      scale={scale}
      onPointerOver={onPointerOver}
      onPointerOut={onPointerOut}
      geometry={geometry}
      material={material}
    />
  );
}

export default CircleMesh;

