import { fetchColumnData } from './api'; // Import the fetchColumnData function

const fetchData = async () => {
  const columns = [
    'X_umap0_norm',
    'X_umap1_norm',
    'global_sphere0_norm',
    'global_sphere1_norm',
    'global_sphere2_norm',
    'clusters',
    'clusters_colors'
  ];

  try {
    const allData = await Promise.all(
      columns.map(col => fetchColumnData(col)) // Use fetchColumnData to fetch each column
    );

    // console.log(allData);

    // Combine the fetched data into a structured format
    const combinedData = allData[0].map((_, index) => {
      return columns.reduce((acc, col, colIndex) => {
        acc[col] = allData[colIndex][index];
        return acc;
      }, {});
    });
    // console.log("hdfa");
    // console.log(combinedData);

    return combinedData;
  } catch (error) {
    console.error("Failed to combine data:", error);
    return [];
  }
};

export default fetchData;
