// Function to fetch data from the API for a given column
const fetchColumnData = async (column) => {
  const baseUrl = "https://fisheyes.techkyra.com/getdata?col=";
  try {
    const response = await fetch(`${baseUrl}${column}`);
    const data = await response.json();
    return JSON.parse(data.data); // Parse the JSON string from the response
  } catch (error) {
    console.error(`Failed to fetch data for column ${column}:`, error);
    return []; // Return an empty array in case of an error
  }
};

export { fetchColumnData };
